@import url("https://fonts.googleapis.com/css?family=Rajdhani:400,500,600,700&display=swap");

body {
  margin: 0;
  overflow: hidden;
  font-family: 'Rajdhani', Arial, sans-serif;
}

.App {
  position: relative;
  width: 100vw;
  height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  background-color: #0A2E15; /* Even darker base */
  background-image: 
    radial-gradient(circle, rgba(34, 255, 0, 0.6) 1px, transparent 1px),
    radial-gradient(circle, rgba(34, 255, 0, 0.6) 1px, transparent 1px);
  background-size: 10px 10px, 10px 10px;
  background-position: 0 0, 5px 5px;
  color: white;
  user-select: none;
}

.top-menu {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: flex-start;
  padding: 10px;
  background-color: #0A2E15; /* Matching the App background */
  box-shadow: 0 2px 8px rgba(34, 255, 0, 0.8); /* Subtle neon glow from top */
}

.profile-area {
  display: flex;
  align-items: center;
  margin-left: 20px;
}

.profile-icon {
  width: 50px; /* Adjust as needed */
  height: 50px; /* Adjust as needed */
  margin-right: 10px;
}

.username {
  font-size: 18px; /* Adjust as needed */
  color: #22ff00; /* Neon green text */
}

.buttons-container {
  position: fixed;
  bottom: 0;
  left: 0;
  width: 100%;
  background-color: #0A2E15;
  padding: 20px 0 30px;
  display: flex;
  justify-content: space-around;
  box-shadow: 0 -2px 8px rgba(34, 255, 0, 0.8); /* Matching neon glow effect */
}

.button {
  flex: 1;
  cursor: pointer;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  transition: transform 0.3s;
}

.button img.icon {
  width: 60px;
  height: 60px;
}

.button:hover img.icon {
  transform: scale(1.1);
}

.button.active img.icon {
  filter: none;
}

@media (max-width: 400px) {
  .buttons-container {
    padding: 10px 0 20px;
  }

  .button img.icon {
    width: 50px;
    height: 50px;
  }

  .button span {
    font-size: 12px;
  }
}
